import demoData from '../models/demo.json'
import Main from "../views/MainView"

class SceneController {

    constructor(){
        this._view = new Main(document.getElementById('container'));
        this._model = demoData;

        this.init();
    }

    init(){
        console.log('initialize it');

        window.addEventListener( 'resize', this.view.resize, false );
    }

    get view() {
        return this._view;
    }

    get model() {
        return this._model;
    }
}

export default SceneController;